import React, { useMemo } from "react";
import { Col, Form, Row } from "antd";
import FiledCard from "../../../../../../components/Cards/FiledCard";
import FormDatePicker from "../../../../../../components/Inputs/FormDatePicker";
import FormTimePicker from "../../../../../../components/Inputs/FormTimePicker";
import FormLabel from "../../../../../../components/Labels/FormLabel";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../../../utilz/i18n";
import { useSelector } from "react-redux";
import { useEffect } from "react";
import {
  convertDateStringtoMoment,
  convertDateToMoment,
  convertTimetoMoment,
  getDateLabel,
} from "../../../../../../../utilz/dates";
import moment from "moment/moment";

export default function MileStones({ form }) {
  const { projectCopy } = useSelector((state) => state.procurementProject);

  const startDate = Form.useWatch("startDate", form);
  const preparationFromDate = Form.useWatch("preparationFromDate", form);
  const preparationToDate = Form.useWatch("preparationToDate", form);
  const bidCollcetionFromDate = Form.useWatch("bidCollcetionFromDate", form);
  const bidcollectionToDate = Form.useWatch("bidcollectionToDate", form);
  const selectionFromDate = Form.useWatch("selectionFromDate", form);
  const selectionToDate = Form.useWatch("selectionToDate", form);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  useEffect(() => {
    form.setFieldsValue({ startDate: moment(),
      preparationFromDate: moment().add(1, 'days'),
      preparationToDate: moment().add(10, 'days'),
      bidCollcetionFromDate: moment().add(11, 'days'),
      bidcollectionToDate: moment().add(21, 'days'),
      selectionFromDate: moment().add(22, 'days'),
      selectionToDate: moment().add(32, 'days'),
      endDate:moment().add(42, 'days')
    })
  }, []);

  useEffect(() => {
    if (projectCopy?.data && Object.keys(projectCopy?.data).length > 0) {
      form.setFieldsValue({
        startDate: convertDateStringtoMoment(projectCopy?.data?.startDate),
        startTime: convertDateStringtoMoment(projectCopy?.data?.startDate),
        endDate: convertDateStringtoMoment(projectCopy?.data?.endDate),
        endTime: convertTimetoMoment(projectCopy?.data?.endTime),
        preparationFromDate: convertDateStringtoMoment(
          projectCopy?.data?.preparationFrom
        ),
        preparationToDate: convertDateStringtoMoment(
          projectCopy?.data?.preparationTo
        ),
        bidCollcetionFromDate: convertDateStringtoMoment(
          projectCopy?.data?.bidCollcetionFrom
        ),
        bidcollectionToDate: convertDateStringtoMoment(
          projectCopy?.data?.bidcollectionTo
        ),
        selectionFromDate: convertDateStringtoMoment(
          projectCopy?.data?.selectionFrom
        ),
        selectionFromTime: convertTimetoMoment(
          projectCopy?.data?.selectionFrom
        ),
        selectionToTime: convertTimetoMoment(projectCopy?.data?.selectionTo),
        selectionToDate: convertDateStringtoMoment(
          projectCopy?.data?.selectionTo
        ),
        preparationFromTime: convertTimetoMoment(
          projectCopy?.data?.preparationFrom
        ),
        bidcollectionToTime: convertTimetoMoment(
          projectCopy?.data?.bidcollectionTo
        ),
        bidCollcetionFromTime: convertTimetoMoment(
          projectCopy?.data?.bidCollcetionFrom
        ),
        preparationToTime: convertTimetoMoment(
          projectCopy?.data?.preparationTo
        ),
      });
    }
  }, [projectCopy]);

  return (
    <Row>
      <Col sm={24}>
        <Row>
          <Col sm={5}>
            <FiledCard
              title={t("translation:procurement.fields.projectLead.label")}
              labelClass="project-milestone-label"
            ></FiledCard>
          </Col>
          <Col sm={5}>
            <FiledCard
              title={t("translation:procurement.projectCard.preparationFiles")}
              labelClass="project-milestone-label"
            ></FiledCard>
          </Col>
          <Col sm={5}>
            <FiledCard
              title={t(
                "translation:procurement.projectCard.bidCollectionPhase"
              )}
              labelClass="project-milestone-label"
            ></FiledCard>
          </Col>
          <Col sm={5}>
            <FiledCard
              title={t("translation:procurement.projectCard.selectionPhase")}
              labelClass="project-milestone-label"
            ></FiledCard>
          </Col>
          <Col sm={4}>
            <FiledCard
              title={t("translation:procurement.fields.date.endDate")}
              labelClass="project-milestone-label"
            ></FiledCard>
          </Col>
        </Row>
        <Row className="mt-15">
          <Col sm={5}>
            <FormLabel className="form-input-label">
              {t("translation:procurement.fields.date.startDateTime")}
            </FormLabel>
            <div className="d-flex gap-1">
              <FormDatePicker
                placeholder={t(
                  "translation:procurement.fields.date.placeholder"
                )}
                margin={"mb-0"}
                name={"startDate"}
                //disableFuture={true}
                disabledTargetDate={preparationFromDate}
                rules={[
                  {
                    required: true,
                    message: "Please enter the Start Date",
                  },
                ]}
              />
              <FormTimePicker
                colon={true}
                editMode={true}
                singleLineMode={false}
                name={"startDate"}
                margin={"m-0"}
                rules={[
                  {
                    required: true,
                    message: "Please enter the Start Time",
                  },
                ]}
              />
            </div>
          </Col>
          <Col sm={5}>
            <FormLabel className="form-input-label">
              {t("translation:procurement.fields.date.startDateTime")}
            </FormLabel>
            <div className="d-flex gap-1">
              <FormDatePicker
                placeholder={t(
                  "translation:procurement.fields.date.placeholder"
                )}
                margin={"mb-0"}
                name={"preparationFromDate"}
                //disableFuture={true}
                disabledTargetDate={startDate}
                rules={[
                  {
                    required: true,
                    message: "Please enter the Preparation From Date",
                  },
                ]}
              />
              <FormTimePicker
                colon={true}
                editMode={true}
                singleLineMode={false}
                name={"preparationFromDate"}
                margin={"m-0"}
                rules={[
                  {
                    required: true,
                    message: "Please enter the Preparation From Time",
                  },
                ]}
              />
            </div>
          </Col>
          <Col sm={5}>
            <FormLabel className="form-input-label">
              {t("translation:procurement.fields.date.startDateTime")}
            </FormLabel>
            <div className="d-flex gap-1">
              <FormDatePicker
                placeholder={t(
                  "translation:procurement.fields.date.placeholder"
                )}
                margin={"mb-0"}
                name={"bidCollcetionFromDate"}
                // disableFuture={true}
                disabledTargetDate={preparationToDate}
                rules={[
                  {
                    required: true,
                    message: "Please enter the Start Date Time",
                  },
                ]}
              />
              <FormTimePicker
                colon={true}
                editMode={true}
                singleLineMode={false}
                name={"bidCollcetionFromDate"}
                margin={"m-0"}
                rules={[
                  {
                    required: true,
                    message: "Please enter the Start Date Time",
                  },
                ]}
              />
            </div>
          </Col>
          <Col sm={5}>
            <FormLabel className="form-input-label">
              {t("translation:procurement.fields.date.startDateTime")}
            </FormLabel>
            <div className="d-flex gap-1">
              <FormDatePicker
                placeholder={t(
                  "translation:procurement.fields.date.placeholder"
                )}
                margin={"mb-0"}
                name={"selectionFromDate"}
                disabledTargetDate={bidcollectionToDate}
                rules={[
                  {
                    required: true,
                    message: "Please enter the Start Date Time",
                  },
                ]}
              />
              <FormTimePicker
                colon={true}
                editMode={true}
                singleLineMode={false}
                name={"selectionFromDate"}
                margin={"m-0"}
                rules={[
                  {
                    required: true,
                    message: "Please enter the Start Date Time",
                  },
                ]}
              />
            </div>
          </Col>
          <Col sm={4}>
            <FormLabel className="form-input-label">
              {t("translation:procurement.fields.date.endDateAndTime")}
            </FormLabel>
            <div className="d-flex gap-1">
              <FormDatePicker
                placeholder={t(
                  "translation:procurement.fields.date.placeholder"
                )}
                margin={"mb-0"}
                name={"endDate"}
                disabledTargetDate={selectionToDate}
                rules={[
                  {
                    required: true,
                    message: "Please enter the Start Date Time",
                  },
                ]}
              />
              <FormTimePicker
                colon={true}
                editMode={true}
                singleLineMode={false}
                name={"endDate"}
                margin={"m-0"}
                rules={[
                  {
                    required: true,
                    message: "Please enter the Start Date Time",
                  },
                ]}
              />
            </div>
          </Col>
        </Row>
        <Row className="mt-15">
          <Col sm={5}></Col>
          <Col sm={5}>
            <FormLabel className="form-input-label">
              {t("translation:procurement.fields.date.endDateAndTime")}
            </FormLabel>
            <div className="d-flex gap-1">
              <FormDatePicker
                placeholder={t(
                  "translation:procurement.fields.date.placeholder"
                )}
                margin={"mb-0"}
                name={"preparationToDate"}
                disabledTargetDate={preparationFromDate}
                rules={[
                  {
                    required: true,
                    message: "Please enter the Start Date Time",
                  },
                ]}
              />
              <FormTimePicker
                colon={true}
                editMode={true}
                singleLineMode={false}
                name={"preparationToDate"}
                margin={"m-0"}
                rules={[
                  {
                    required: true,
                    message: "Please enter the Start Date Time",
                  },
                ]}
              />
            </div>
          </Col>
          <Col sm={5}>
            <FormLabel className="form-input-label">
              {t("translation:procurement.fields.date.endDateAndTime")}
            </FormLabel>
            <div className="d-flex gap-1">
              <FormDatePicker
                placeholder={t(
                  "translation:procurement.fields.date.placeholder"
                )}
                margin={"mb-0"}
                name={"bidcollectionToDate"}
                disabledTargetDate={bidCollcetionFromDate}
                rules={[
                  {
                    required: true,
                    message: "Please enter the Start Date Time",
                  },
                ]}
              />
              <FormTimePicker
                colon={true}
                editMode={true}
                singleLineMode={false}
                name={"bidcollectionToDate"}
                margin={"m-0"}
                rules={[
                  {
                    required: true,
                    message: "Please enter the Start Date Time",
                  },
                ]}
              />
            </div>
          </Col>
          <Col sm={5}>
            <FormLabel className="form-input-label">
              {t("translation:procurement.fields.date.endDateAndTime")}
            </FormLabel>
            <div className="d-flex gap-1">
              <FormDatePicker
                placeholder={t(
                  "translation:procurement.fields.date.placeholder"
                )}
                margin={"mb-0"}
                name={"selectionToDate"}
                // disableFuture={true}
                disabledTargetDate={selectionFromDate}
                rules={[
                  {
                    required: true,
                    message: "Please enter the Start Date Time",
                  },
                ]}
              />
              <FormTimePicker
                colon={true}
                editMode={true}
                singleLineMode={false}
                name={"selectionToDate"}
                margin={"m-0"}
                rules={[
                  {
                    required: true,
                    message: "Please enter the Start Date Time",
                  },
                ]}
              />
            </div>
          </Col>
          <Col sm={4}></Col>
        </Row>
      </Col>
    </Row>
  );
}
