import { Col, Form, Row } from "antd";
import { useEffect, useState, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { NOTIFICATION_TYPES } from "../../../../../../contants/common";
import {
  createUpdateProjectInProgress,
  createUpdateProjectRequestReset,
  getProjectCopyRequestReset,
} from "../../../../../../redux/Procurement/Project/ProcurementProjectActions";
import {
  dateTimeToSingleValue,
  getFormattedTimeString,
} from "../../../../../../utilz/dates";
import {
  triggerNotification,
  validateForm,
  validateForms,
} from "../../../../../../utilz/utilz";
import FiledCard from "../../../../../components/Cards/FiledCard";
import Paper from "../../../../../components/Paper/Paper";
import SaveCancelButtons from "../../../../Shipment/components/SaveCancelButtons";
import AddProjectTabs from "./AddProjectTabs";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../../utilz/i18n";
import { setAppLoader } from "../../../../../../redux/common/CommonActions";

function AddProject() {
  const [form] = Form.useForm();
  const [projectDetailsForm] = Form.useForm();
  const [mileStoneForm] = Form.useForm();
  const [documentForm] = Form.useForm();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { projectCopy, userProcurement, createUpdateProject } = useSelector(
    (state) => state.procurementProject
  );
  const [activeKey, setActiveKey] = useState(["1", "2", "3"]);
  const notificationAPI = useSelector((state) => state.common.notificationAPI);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  useEffect(() => {
    if (createUpdateProject?.data?.result === "Failed") {
      dispatch(setAppLoader(false))
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: createUpdateProject?.data?.message,
      });
    } else if (createUpdateProject.success === true) {
      dispatch(setAppLoader(false))
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.SUCCESS,
        title: t("translation:procurement.projectCard.notificationSuccess"),
      });
      dispatch(createUpdateProjectRequestReset());
      navigate(-1);
    }
    if (createUpdateProject?.error === true) {
      dispatch(setAppLoader(false))
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: t("translation:procurement.projectCard.notificationError"),
      });
      dispatch(createUpdateProjectRequestReset());
    }
  }, [createUpdateProject]);

  const createUpdateProjectHandler = () => {};

  const handleSubmit = async (submittedForm, { forms }) => {
    if (submittedForm === "saveButtonForm") {
      try {
        const formList = [
          validateForms(forms.projectDetailsForm, "projectDetailsForm"),
          validateForms(forms.mileStoneForm, "mileStoneForm"),
          // validateForms(forms?.fileupdate, "documentForm"),
        ];
        await Promise.all(formList);
      } catch (e) {
        console.log(e);
        return;
      }

      const projectFormValue = { ...projectDetailsForm.getFieldValue() };
      const documentFormValue = { ...documentForm.getFieldValue() };
      const mileStoneFormValues = { ...mileStoneForm.getFieldValue() };

      const projectLeadList = projectFormValue?.projectLeadList
        ?.toString()
        ?.split(",");
      const projectSupportList = projectFormValue?.projectSupportList
        ?.toString()
        ?.split(",");
      const projectTechnicalSupportList =
        projectFormValue?.projectTechnicalSupportList?.toString()?.split(",");

      const getFormattedTime = (fieldName) =>
        getFormattedTimeString(mileStoneFormValues[fieldName]);

      const req = {
        projectID:projectFormValue?.projectID || 0,
        projectName: projectFormValue?.projectName || "",
        description: projectFormValue?.description || "",
        isActive: projectFormValue?.isActive || false,
        isApproved: projectFormValue?.isApproved || false,
        projectLeadList,
        projectSupportList,
        projectTechnicalSupportList,
        startDate: getFormattedTime("startDate"),
        preparationFrom: getFormattedTime("preparationFromDate"),
        preparationTo: getFormattedTime("preparationToDate"),
        bidCollcetionFrom: getFormattedTime("bidCollcetionFromDate"),
        bidcollectionTo: getFormattedTime("bidcollectionToDate"),
        selectionFrom: getFormattedTime("selectionFromDate"),
        selectionTo: getFormattedTime("selectionToDate"),
        endDate: getFormattedTime("endDate"),
        projectDocumentInformation: [...documentFormValue?.documents],
      };
      dispatch(setAppLoader(true))
      dispatch(createUpdateProjectInProgress(req));
    }
  };

  return (
    <Form.Provider
      className={"overflow-hidden h-100"}
      onFormFinish={handleSubmit}
    >
      <Row
        className={"flex-direction-column flex-nowrap overflow-hidden h-100"}
      >
        <Col className="mb-15">
          <Paper className={"p-15 border-radius-bottom-none"}>
            <Row align="middle" justify="space-between">
              <Col>
                <Form form={form}>
                  <FiledCard title={"Create Project"}></FiledCard>
                </Form>
              </Col>
              <Col>
                <Form name="saveButtonForm">
                  <SaveCancelButtons
                    onSave={() => createUpdateProjectHandler()}
                    onCancel={() => navigate(-1)}
                  />
                </Form>
              </Col>
            </Row>
          </Paper>
        </Col>
        <Col className="flex-1 custom-scroll-bar">
          <AddProjectTabs
            form={form}
            projectDetailsForm={projectDetailsForm}
            mileStoneForm={mileStoneForm}
            documentForm={documentForm}
            activeKey={activeKey}
            setActiveKey={setActiveKey}
          />
        </Col>
      </Row>
    </Form.Provider>
  );
}
export default AddProject;
