import React, { useMemo } from "react";
import { Col, Row } from "antd";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  checkProjectNameIsExists,
  getUserProcurementInProgress,
} from "../../../../../../../redux/Procurement/Project/ProcurementProjectActions";
import FormInput from "../../../../../../components/Inputs/FormInput";
import FormSelectBox from "../../../../../../components/Inputs/FormSelectBox";
import FormTextArea from "../../../../../../components/Inputs/FormTextArea";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../.././../../utilz/i18n";
import { checkProjectNameEffects } from "../../../../../../../redux/Procurement/Project/ProcurementProjectEffects";
import HiddenFormInput from "../../../../../../components/Inputs/HiddenFormInput";

export default function ProjectDetails({ form }) {
  const dispatch = useDispatch();
  const [projectSupportOptions, setProjectSupportOptions] = useState([]);
  const [techLeadOptions, setTechLeadOptions] = useState([]);
  const [projectLeadOptions, setProjectLeadOptions] = useState([]);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const {
    selectedCompany,
    user: { data: userDetails },
  } = useSelector((state) => state.auth);
  const [isCopyProject, setIsCopyProject] = useState(false);
  const { userProcurement, checkProjectName, projectCopy } = useSelector(
    (state) => state.procurementProject
  );

  useEffect(() => {
    form.setFieldsValue({
      projectID:0
    })
  }, [])
  

  useEffect(() => {
    const projectData = projectCopy?.data;

    if (projectData && Object.keys(projectData).length) {
      form.setFieldsValue({
        ...projectData,
        projectID:0,
        projectTechnicalSupportList:
          projectData.projectTechnicalSupportList?.map((item) => item.value) ??
          [],
          projectSupportList:
          projectData.projectSupportList?.map((item) => item.value) ?? [],
          projectLeadList:
          projectData.projectLeadList?.map((item) => item.value) ?? [],
      });

      setIsCopyProject(true);
    }
  }, [projectCopy]);

  useEffect(() => {
    if (!userProcurement?.success) {
      dispatch(
        getUserProcurementInProgress({ userType: userDetails?.userType })
      );
    }
  }, []);

  useEffect(() => {
    const mapData = (e) =>
      userProcurement?.data?.[e]?.map(({ userName, email, userId }) => ({
        label: `${userName} (${email})`,
        value: userId+'',
      }));
    setProjectSupportOptions(mapData("22"));
    setTechLeadOptions(mapData("23"));
    setProjectLeadOptions(mapData("21"));

    form.setFieldsValue({ isActive: true });
    form.setFieldsValue({ isApproved: false });
  }, [userProcurement?.success]);

  const checkProjectNameHandler = async (rule, value) => {
    const getProjectName = value;

    if (!getProjectName) {
      return;
    }

    const currentDate = new Date();
    const year = currentDate.getFullYear();
    const month = currentDate.getMonth() + 1;
    const companyKey = selectedCompany?.companyId;

    try {
      const checkProjectNameExist = await checkProjectNameEffects({
        ProjectName: getProjectName,
      });

      if (checkProjectNameExist?.data) {
        const actualProjectName = `${getProjectName}/${companyKey}/${year}/${month}`;
        form.setFieldsValue({ actualProjectName });
        return Promise.resolve();
      } else {
        form.setFieldsValue({ actualProjectName: "" });
        throw new Error(
          t("translation:default.fields.projectName.messageExist")
        );
      }
    } catch (error) {
      return Promise.reject(error);
    }
  };

  return (
    <Row>
      <Col sm={24}>
        <Row className="mb-15">
          <Col sm={6} className="pr-15">
            <HiddenFormInput name="projectID"/>
            <FormInput
              editMode={true}
              label={t("translation:default.fields.projectName.label")}
              placeholder={t("translation:default.fields.projectName.label")}
              name="projectName"
              validateTrigger="onBlur"
              rules={[
                {
                  required: true,
                  message: t("translation:procurement.projectCard.message"),
                },
                {
                  validator: checkProjectNameHandler,
                },
              ]}
            />
          </Col>
          <Col sm={6} className="pr-15">
            <FormSelectBox
              loading={false}
              options={[
                {
                  label: t("translation:procurement.fields.projectStatus.true"),
                  value: true,
                },
                {
                  label: t(
                    "translation:procurement.fields.projectStatus.false"
                  ),
                  value: false,
                },
              ]}
              placeholder={t("translation:default.buttons.select")}
              label={t("translation:procurement.fields.projectStatus.label")}
              rules={[
                {
                  required: true,
                  message: t(
                    "translation:procurement.fields.projectStatus.message"
                  ),
                },
              ]}
              name={"isActive"}
              editMode={false}
              disabled={isCopyProject}
            />
          </Col>
          <Col sm={6}>
            <FormSelectBox
              loading={false}
              options={projectLeadOptions}
              placeholder={t("translation:default.buttons.select")}
              label={t("translation:procurement.fields.projectLead.label")}
              rules={[
                {
                  required: true,
                  message: t(
                    "translation:procurement.fields.projectLead.message"
                  ),
                },
              ]}
              name={"projectLeadList"}
              editMode={true}
              className="pr-15"
              mode="multiple"
            />
          </Col>
          <Col sm={6}>
            <FormSelectBox
              loading={false}
              options={techLeadOptions}
              placeholder={t("translation:default.buttons.select")}
              label={t("translation:procurement.fields.tecLead.label")}
              rules={[
                {
                  required: true,
                  message: t("translation:procurement.fields.tecLead.message"),
                },
              ]}
              name={"projectTechnicalSupportList"}
              editMode={true}
              mode="multiple"
            />
          </Col>
        </Row>
        <Row className="mb-15">
          <Col sm={6}>
            <FormInput
              loading={false}
              placeholder={t("translation:default.fields.projectName.label")}
              label={t("translation:procurement.fields.actualName.label")}
              rules={[]}
              name={"actualProjectName"}
              editMode={true}
              className="pr-15"
              disabled={true}
            />
          </Col>
          <Col sm={6}>
            <FormSelectBox
              loading={false}
              options={[
                {
                  label: t("translation:default.options.yes"),
                  value: true,
                },
                {
                  label: t("translation:default.options.no"),
                  value: false,
                },
              ]}
              placeholder={t(
                "translation:procurement.fields.projectApproved.label"
              )}
              label={t("translation:procurement.fields.projectApproved.label")}
              rules={[
                {
                  required: true,
                  message: t(
                    "translation:procurement.fields.projectApproved.message"
                  ),
                },
              ]}
              name={"isApproved"}
              editMode={false}
              className="pr-15"
              disabled={isCopyProject}
            />
          </Col>
          <Col sm={6}>
            <FormSelectBox
              loading={false}
              options={projectSupportOptions}
              placeholder={t("translation:default.buttons.select")}
              label={t("translation:procurement.fields.projectSupport.label")}
              rules={[
                {
                  required: true,
                  message: t(
                    "translation:procurement.fields.projectSupport.message"
                  ),
                },
              ]}
              name={"projectSupportList"}
              editMode={true}
              className="pr-15"
              mode="multiple"
            />
          </Col>
        </Row>
        <Row className="mb-15">
          <Col sm={6}>
            <FormTextArea
              name="description"
              label={t("translation:default.fields.description")}
              placeholder={t("translation:default.buttons.select")}
              editMode={true}
              rows={2}
              enableAutoFocus={false}
              fillHeight={true}
              className="pr-15"
              rules={[
                {
                  required: true,
                  message: t(
                    "translation:procurement.fields.description.message"
                  ),
                },
              ]}
            />
          </Col>
        </Row>
      </Col>
    </Row>
  );
}
