import { useMemo } from "react";
import { useLocation } from "react-router-dom";
import { Col, Form, Row } from "antd";
import SvgConverter from "../../../../../components/Common/SvgConverter";
import { ReactComponent as ShipmentIcon } from "../../../../../../assets/icons/shipment-file.svg";
import { ReactComponent as TruckIcon } from "../../../../../../assets/icons/truck.svg";
import { ReactComponent as DocsListIcon } from "../../../../../../assets/icons/docs-list.svg";
import CollapsiblePanel from "./../../../../../components/Tabs/CollapsiblePanel";

import FileDetails from "./Component/FileDetails";
import ProjectDetails from "./Component/ProjectDetails";
import MileStones from "./Component/MileStones";

import { routeFileSelector } from "../../../../../../utilz/i18n";
import { useTranslation } from "react-i18next";
import { useEffect } from "react";
import { useSelector } from "react-redux";

const AddProjectTabs = ({
  projectDetailsForm,
  mileStoneForm,
  documentForm,
  activeKey,
  setActiveKey,
}) => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  // const { projectCopy } = useSelector((state) => state.procurementProject);

  // useEffect(() => {
  //     if (projectCopy?.data) {
  //         projectDetailsForm.setFieldsValue({
  //           ...projectCopy?.data,
  //         });
  //       form.setFieldsValue({
  //         ...projectCopy?.data,
  //       });
  //       // documentForm.setFieldsValue({
  //       //   ...projectCopy?.data,
  //       // });
  //     }
  //   }),
  //     [];
  const GenerateTitle = ({ title, Icon, size = 25 }) => {
    return (
      <Row gutter={10} align={"middle"}>
        <Col className="lh-0">
          <SvgConverter width={size} height={size} Icon={Icon} />
        </Col>
        <Col className="lh-0">
          <label className="lh-0 text-dark cursor-pointer">{title}</label>
        </Col>
      </Row>
    );
  };

  return (
    <CollapsiblePanel
      forceRender={true}
      handleTabClick={(keys) => setActiveKey(keys)}
      activeKey={activeKey}
      tabs={[
        {
          content: (
            <Form form={projectDetailsForm} name="projectDetailsForm">
              <ProjectDetails form={projectDetailsForm} />
            </Form>
          ),
          header: (
            <GenerateTitle
              title={t("translation:procurement.projectTabs.projectDetails")}
              Icon={ShipmentIcon}
              size={25}
            />
          ),
        },
        {
          content: (
            <Form form={mileStoneForm} name="mileStoneForm">
              <MileStones form={mileStoneForm} />
            </Form>
          ),
          header: (
            <GenerateTitle
              title={t("translation:procurement.projectCard.mileStones")}
              Icon={TruckIcon}
              size={25}
            />
          ),
        },
        {
          content: (
            <Form form={documentForm} name="documentForm">
              <FileDetails form={documentForm} />
            </Form>
          ),
          header: (
            <GenerateTitle
              title={t("translation:default.documentManager.title")}
              Icon={DocsListIcon}
              size={25}
            />
          ),
        },
      ]}
    />
  );
};

export default AddProjectTabs;
