import React, { Fragment, useState, useMemo, useEffect } from "react";
import FormInput from "../../../../../../components/Inputs/FormInput";
import { ReactComponent as DownloadIcon } from "../../../../../../../assets/icons/download-icon.svg";
import { ReactComponent as EditIcon } from "../../../../../../../assets/icons/edit-icon.svg";
import { ReactComponent as CloseIcon } from "../../../../../../../assets/icons/close-icon.svg";
import { Button, Card, Col, Form, Popconfirm, Row } from "antd";
import FiledCard from "../../../../../../components/Cards/FiledCard";
import Files from "react-files";
import HiddenFormInput from "../../../../../../components/Inputs/HiddenFormInput";
import DocumentTypeSelector from "../../../../../../components/Selectors/DocumentTypeSelector";
import { MODULES_ID } from "../../../../../../../contants/common";
import FormTextArea from "../../../../../../components/Inputs/FormTextArea";
import SaveCancelButtons from "../../../../../../components/Buttons/SaveCancelButtons";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../../../utilz/i18n";

export default function ProcurementFileDetailsCard({
  props,
  isNew,
  fileDetails,
  handleSave,
  handleDelete,
  index,
  allowedExtentions,
  editRecord,
  handleResetValueAction = () => {},
  isProjectUpload = false,
  renderKey = null,
}) {
  const [form] = Form.useForm();
  const [file, setFile] = useState(false);
  const fileName = Form.useWatch("file", form);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const [editMode, setEditMode] = useState(Boolean(fileDetails.isNew));

  useEffect(() => {
    setEditMode(Boolean(fileDetails.isNew));
  }, [fileDetails.isNew]);

  const handleFileChange = (files) => {
    try {
      if (files.length > 0) {
        setFile(files[0]);
        form.setFieldsValue({ file: files[0].name });
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handleFileError = (error) => {
    try {
      if (error) {
        form.setFields([
          {
            name: "file",
            errors: [error.message],
          },
        ]);
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handleFileSubmit = (data) => {
    const formData = new FormData();
    formData.append("ProjectID", fileDetails.projectID);
    formData.append("TenderID", fileDetails?.tenderID || "");
    formData.append("DocType", data.docTypeCode ? data.docTypeCode : "");
    formData.append(
      "FileComment",
      data.documentTitle ? data.documentTitle : ""
    );
    if (file) {
      formData.append("File", file);
    }
    if (isNew === false) {
      formData.append("DocumentID", fileDetails?.documentID);
    }
    if (isProjectUpload) {
      if(file){
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = function (event) {
          let req = {
            documentID: fileDetails?.documentID || 0,
            projectID: fileDetails.projectID || 0,
            tenderID: fileDetails?.tenderID || 0,
            documentPath: reader.result.substr(
              event.target.result.indexOf(",") + 1
            ),
            documentName: file.name || "",
            docType: data.docTypeCode ? data.docTypeCode : "",
            comments: data.documentTitle ? data.documentTitle : "",
            fileExtension: file?.extension || "",
            renderKey: renderKey,
          };
  
          handleSave(formData, isNew, req);
        };

      } else {
        let req = {
            documentID: fileDetails?.documentID || 0,
            projectID: fileDetails.projectID || 0,
            tenderID: fileDetails?.tenderID || 0,
            documentPath: fileDetails?.documentPath || '',
            documentName: fileDetails.documentName || "",
            docType: data.docTypeCode ? data.docTypeCode : "",
            comments: data.documentTitle ? data.documentTitle : "",
            renderKey: renderKey,
          };
          debugger;
          handleSave(formData, isNew, req);
      }
      
      
    } else {
      handleSave(formData, isNew, index);
    }
  };

  return (
    <Form
      form={form}
      name="fileupdate"
      onFinish={handleFileSubmit}
      initialValues={fileDetails}
      {...{
        initialValues: fileDetails?.fileGuid
          ? {
              docTypeCode: fileDetails?.docType,
              documentTitle: fileDetails?.comments,
              documentDesc: fileDetails?.fileDescription,
              file: fileDetails?.file,
            }
          : {},
      }}
    >
      <Card
        title={
          <div
            className={`d-flex gap-1 ${
              fileDetails?.isUpdate === false ? "align-items-center" : ""
            }`}
          >
            {fileDetails?.isUpdate === false && (
              <Fragment>
                <FiledCard
                  title={t("translation:default.fields.fileName")}
                  textClass={
                    "d-flex align-items-center gap-1 justify-content-center"
                  }
                  childClassName={"d-flex align-item-start"}
                >
                  <HiddenFormInput
                    name="file"
                    rules={[
                      {
                        required: true,
                        message: t(
                          "translation:default.documentManager.chooseFileMessage"
                        ),
                      },
                    ]}
                  />
                  <label className="f-14 fw-600">{fileName}</label>
                </FiledCard>
              </Fragment>
            )}

            {fileDetails?.isUpdate && allowedExtentions && (
              <Fragment>
                <FiledCard
                  title={t("translation:default.fields.fileName")}
                  textClass={"d-flex align-items-center gap-1"}
                >
                  <Files
                    className="files-dropzone"
                    onChange={handleFileChange}
                    onError={handleFileError}
                    accepts={allowedExtentions}
                    multiple={false}
                    // maxFileSize={10000000}
                    minFileSize={0}
                    clickable
                  >
                    <Button className="auction-btn w-100 border-radion-5 white-font">
                      {t("translation:default.documentManager.chooseFile")}
                    </Button>
                  </Files>

                  <HiddenFormInput
                    name="file"
                    rules={[
                      {
                        required: true,
                        message: t(
                          "translation:default.documentManager.chooseFileMessage"
                        ),
                      },
                      () => ({
                        validator(_, value) {
                          if (!value) {
                            return Promise.resolve();
                          }
                          let extension = value.match(/\.([^.]+)$/)[1];
                          extension = `.${extension}`;
                          if (allowedExtentions.includes(extension)) {
                            return Promise.resolve();
                          } else if (!allowedExtentions.includes(extension)) {
                            return Promise.reject(
                              new Error(
                                `${t(
                                  "translation:default.documentManager.only"
                                )} ${allowedExtentions.join(",")} ${t(
                                  "translation:default.documentManager.filesAreAllowed"
                                )} `
                              )
                            );
                          }
                        },
                      }),
                    ]}
                  />
                </FiledCard>
                <label className="f-14 fw-600">{fileName}</label>
              </Fragment>
            )}
          </div>
        }
        className="mb-15 paper-container visibility-file-card procurement-file-upload"
        extra={
          <div className="d-flex gap-1">
            {fileDetails?.isUpdate && (
              <Fragment>
                <SaveCancelButtons
                  primaryBtnType="button"
                  setEditMode={setEditMode}
                  editMode={editMode}
                  onSave={() => form.submit()}
                  onCancel={() => {
                    handleResetValueAction(index, fileDetails.isNew);
                  }}
                />
              </Fragment>
            )}
            {!fileDetails?.isUpdate && (
              <Fragment>
                {isProjectUpload === false && (
                  <DownloadIcon
                    className="cursor-pointer"
                    onClick={() =>
                      window.location.assign(fileDetails?.cloudUrl?.[0])
                    }
                  />
                )}

                <EditIcon
                  onClick={() => editRecord(index)}
                  className="cursor-pointer"
                />
                <Popconfirm
                  placement="topRight"
                  title={"Are you sure to delete?"}
                  onConfirm={() => handleDelete(fileDetails, index)}
                  okText="Yes"
                  cancelText="Cancel"
                  overlayClassName=""
                >
                  <CloseIcon
                    className="cursor-pointer"
                  />
                </Popconfirm>
              </Fragment>
            )}
          </div>
        }
      >
        <Row>
          <Col sm={8} className="pr-15">
            <DocumentTypeSelector
              editMode={fileDetails?.isUpdate || false}
              margin={"mb-16"}
              name={"docTypeCode"}
              moduleId={MODULES_ID.SHIPMENT}
              onChange={({ label }) =>
                form.setFieldsValue({ documentDesc: label })
              }
              // rules={[
              
              //   () => ({
              //     validator(_, value) {
              //       if (file && value) {
              //         return Promise.resolve();
              //       } else {
              //         return Promise.reject(
              //           new Error(
              //             "Please select the document type"
              //           )
              //         );
              //       }
              //     },
              //   }),
              // ]}
            />
          </Col>
          <Col sm={16} className="pr-15">
            <FormTextArea
              label={t(
                "translation:default.documentManager.fileComments.label"
              )}
              editMode={fileDetails?.isUpdate}
              margin={"mb-0"}
              name={"documentTitle"}
              placeholder={"Description"}
              enableAutoFocus={false}
              rows={fileDetails?.isUpdate ? 4 : 1}
              maxLength={500}
              // rules={[
              
              //   () => ({
              //     validator(_, value) {
              //       if (file && value) {
              //         return Promise.resolve();
              //       } else {
              //         return Promise.reject(
              //           new Error(
              //             "Please enter the description"
              //           )
              //         );
              //       }
              //     },
              //   }),
              // ]}
            />
          </Col>
        </Row>
      </Card>
      <HiddenFormInput name="documentDesc" />
    </Form>
  );
}
